import freeBase from "@/common/mixin/free-base.js"
import linAvater from "@/components/lin-ui/lin-avater.vue"
import linBadge from "@/components/lin-ui/lin-badge.vue"
export default {
	mixins: [freeBase],
	components: {
		linAvater,
		linBadge
	},
	props: {
		item: Object,
		index: Number,
		backClass: String
	},
	methods: {
		onClick() {
			this.$emit('click')
		},
		long(e) {
			console.log(e);
			let x = 0
			let y = 0







			x = e.detail.x
			y = e.detail.y

			this.$emit('long',{x,y,index:this.index})
		}
	}
}
